*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  height: 100vh;
  width: 100%;
  #root {
    height: 100%;
  }
}
.circle-wrapper {
  position: relative;
  .circle {
    &-timer__work, &-timer__rest {
      overflow: hidden;
      white-space: nowrap;
      position: absolute;
      // top: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      left: -35px;
      // width: 100px;
      height: 25px;
      transform: translate(160px, -170px);
      // display: flex;
      align-items: center;
      justify-content: center;
      font-family: "Montserrat", sans-serif;
      font-size: 1.2rem;
    }
    &-timer__work {
      transform: translate(160px, -195px);

    }
  }
}
.circle {
  // box-shadow: inset 1px 10px 3px 1px black;
  border-radius: 50%;
  // border: 1px solid black;
  position: relative;
  width: 340px;
  height: 340px;

}


.circle-svg {
  width: 340px;
  height: 340px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: rotateZ(-90deg);
}

.circle::after {
  content: "";
  top: 5px;
  left: 6px;
  position: absolute;
  width: 330px;
  height: 330px;
  border: 5px solid rgb(194, 194, 194);
  border-radius: 50%;
  // background-color: gray;
}

.circle::before {
  content: "";
  top: 30px;
  left: 30px;
  position: absolute;
  width: 280px;
  height: 280px;
  border: 5px solid rgb(194, 194, 194);
  border-radius: 50%;
  // background-color: gray;
}

.wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

}
.timer__input-input-1, .timer__input-input-2 {
  opacity: 0;
  position: relative;

}
.timer {
  width: 150px;
}

.timer__reset {
  overflow: hidden;
  cursor: pointer;
  white-space: nowrap;
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  height: 50px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  justify-self: flex-start;
  align-self: center;
  background: linear-gradient(45deg, rgba(255, 165, 0, .5) 30%, rgba(255, 215, 0, .5) 40%, red 100%);

}

.timer__reset-wrapper {
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  height: 50px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  justify-self: flex-start;
  align-self: center;
}
.timer__numbers {
  margin: 0;

  width: 150px;
  border-right: 1px solid white;

  transition: border-right 0.3s;
}
.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 50px;

}
span {
  font-size: 2rem;
  font-family: "Montserrat", sans-serif;
}
.h1-text {
  font-size: 0.75rem;
}
.active {
  border-right: 1px solid black;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  background: linear-gradient(45deg, rgba(255, 165, 0, .5) 30%, rgba(255, 215, 0, .5) 40%, red 100%);
  // background-color: gray;
  overflow: hidden;
  white-space: nowrap;
}

.input-fields {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
}

.modal {
  
  position: absolute;
  top: 300px;
  left: 100px
}

.active:hover {
  border-right: 1px solid black;
}

.timer__input::after {
  content: "Work?";
  position: absolute;
  width: 200px;
  height: 20px;
  top: 31px;
  left: 95px;
  font-size: 0.75rem;
  color: black;
  font-family: "Montserrat", sans-serif;
  z-index: 10;
}

.timer__input:nth-child(2)::before {
  
  content: "Rest?";
  position: absolute;
  width: 200px;
  height: 20px;
  top: 89px;
  left: 100px;
  font-size: 0.75rem;
  color: black;
  font-family: "Montserrat", sans-serif;
  z-index: 10;
}
.cycle-circles {
  width: 35px;
  height: 175px;
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  position: relative;
}
.circle-counter {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  outline: 2px solid rgb(165, 165, 165);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  position: relative;
  &__inside {
    z-index: -1;
    position: absolute;
    // transition: width 1s;
    // width: 10px;
    // height: 10px;
    background: linear-gradient(45deg, rgba(255, 215, 0, .5) 40%, red 100%);
    border-radius: 50%;
  }
}
.cycle-circles::after {
  position: absolute;
  content: "Cycles?";
  width: 50px;
  height: 20px;
  top: 175px;
  left: -16px;
  color: black;
  font-family: "Montserrat", sans-serif;
  font-size: 0.75rem;
  font-weight: 500;
}
.container-first-wrapper {
  display: flex;
  width: fit-content;
  // flex-direction: column;
  gap: 20px;
}
.container-second-wrapper {
  display: flex;
  align-items: center;
  gap: 25px;
}

@media only screen and (max-width: 800px) {
  .container {
    flex-direction: column;
    width: 100%;
    // height: 100%;
  }
}
@media only screen and (max-width: 900px) {
  .container-first-wrapper {
  display: flex;
  width: fit-content;
  flex-direction: column;
  gap: 20px;
}
}
.cycle-counter {
  position: absolute;
  top: 203px;
  left: 140px;
  font-family: "Montserrat", sans-serif;
  font-size: 0.75rem;
  font-weight: 600;
}